import React, { Component } from 'react';
import { Link, Route, Switch } from 'react-router-dom';

import Home from './components/home/Home';
import Reservation from './components/reservation/Reservation';
import Report from "./components/report/Report";
import Login from './components/login/Login';
import Master from './components/master/Master';
import Settings from './components/settings/Settings';

import logo from './img/logo.png';

class App extends Component {
	render() {
		return (
			<div>
				<Header />
					<Switch>
						<Route exact path="/" component={Home} />
						<Route path="/reservation" component={Reservation} />
						<Route path="/reports" component={Report} />
						<Route path="/login" component={Login} />
						<Route path="/master" component={Master} />
						<Route path="/settings" component={Settings} />
					</Switch>
				<Footer />
			</div>
		);
	}
}

const Header = () => {
	return (
		<header id="header">
			<div className="container">
				<div className="row align-items-center justify-content-between d-flex">
					<div id="logo">
						<a href="index.html"><img src={logo} alt="" title="" /></a>
					</div>
					<nav id="nav-menu-container">
						<ul className="nav-menu">
							<li><Link to="/">Home</Link></li>
							<li><Link to="/reservation">Reservation</Link></li>
							<li><Link to="/reports">Reports</Link></li>
							<li><Link to="/master">Master Data</Link></li>
							<li><Link to="/settings">Settings</Link></li>
							<li><Link to="/login">Log Out</Link></li>
						</ul>
					</nav>{/* #nav-menu-container */}
				</div>
			</div>
		</header>
	);
}

const Footer = () => {
	return (
		<footer className="footer-area">
			<div className="container">
				<div className="row">
					<div className="col-lg-6 col-md-6 col-sm-6">
						<div className="single-footer-widget">
							<h6>About us</h6>
							<p>
								Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore dolore magna aliqua.
          					</p>
						</div>
					</div>
					<div className="col-lg-6 col-md-6 col-sm-6">
						<div className="single-footer-widget">
							<h6>Follow Us</h6>
							<p>Let us be social</p>
							<div className="footer-social d-flex align-items-center">
								<a href="#"><i className="fa fa-facebook" /></a>
								<a href="#"><i className="fa fa-twitter" /></a>
								<a href="#"><i className="fa fa-dribbble" /></a>
								<a href="#"><i className="fa fa-behance" /></a>
							</div>
						</div>
					</div>
				</div>
				<div className="footer-bottom d-flex justify-content-center align-items-center flex-wrap">
					<p className="footer-text m-0">Copyright  2018 All rights reserved to <i className="fa fa-heart-o" aria-hidden="true" /> by <a href="http://pixelcoder.net">Pixel Coder</a></p>
				</div>
			</div>
		</footer>
	)
}

export default App;



