import React, { Component } from 'react';
import TopArea from '../fragments/TopArea';

class Report extends Component {
	state = {
		"data": [
			{
				"name": "Freddymraf",
				"division": null,
				"room": "Meeting Room 01",
				"clientCode": "ASTRA",
				"guest": "Mr. Ronald",
				"estimatedTime": {
					"start": "2018-08-21 08:00",
					"end": "2018-08-21 10:00"
				},
				"invitee": "Ihsanalfarabi97"
			}
		]
	}

	componentDidMount() {
		document.title = "Report";
	}

	render() {
		const { data } = this.state;
		const rows = data.map((row, index) => {
			return (
				<tr>
					<td>{row.room}</td>
					<td>{row.estimatedTime.start}</td>
					<td>{row.estimatedTime.end}</td>
					<td>{row.division}</td>
					<td>{row.name}</td>
					<td>{row.guest}</td>
					<td>{row.clientCode}</td>
					<td>{row.invitee}</td>
				</tr>
			);
		});
		
		return (
			<div>
				<TopArea title="Reports" description="Office App" />
				<section className="section-gap">
				<div className="container">
					<div className="row d-flex justify-content-center pb-30">
						<div className="col-lg-8">
							<div className="section-title text-center">
								<h1>Meeting Room Occupancy</h1>
							</div>
						</div>
					</div>
					<div className="row">
						<div className="form-group col-md-3">
							<label htmlFor="date">Date</label>
							<input type="text" className="form-control" id="date" aria-describedby="date" placeholder="Enter date" />
						</div>
						<div className="form-group col-md-3">
							<label htmlFor="date">From Date</label>
							<input type="text" className="form-control" id="from-date" aria-describedby="from-date" placeholder="Enter from date" />
						</div>
						<div className="form-group col-md-3">
							<label htmlFor="date">To Date</label>
							<input type="text" className="form-control" id="to-date" aria-describedby="to-date" placeholder="Enter to date" />
						</div>
						<div className="form-group col-md-3 align-self-end">
							<div className="btn-group d-flex" role="group" aria-label="Basic example">
								<button type="button" className="btn btn-primary w-100">Filter</button>
								<button type="button" className="btn btn-secondary w-100">Reset</button>
							</div>
						</div>
					</div>
					<div className="table-responsive">
						<table className="tile table table-bordered table-striped">
							<thead>
								<tr>
									<th>
										Meeting Room
									</th>
									<th>
										<a>Estimate Start Time</a>
									</th>
									<th>
										<a>Estimate End Time</a>
									</th>
									<th>
										Division
									</th>
									<th>
										Applicant
									</th>
									<th>
										Guest Name
									</th>
									<th>
										Client Code
									</th>
									<th>
										Invitee
									</th>
								</tr>
							</thead>
							<tbody>
								{rows}
							</tbody>
						</table>
					</div>
				</div>
			</section>
			</div>
		);
	}
}

export default Report;


