import React, { Component } from 'react';

import TopArea from '../fragments/TopArea';
import Modal from './Modal';

class Master extends Component {
    state = {
        data: [
            {
                'name': 'Meeting Room 01',
                'capacity': '10',
                'status': ''
            },
            {
                'name': 'Meeting Room 02',
                'capacity': '20',
                'status': ''
            }
        ]
    }

    componentDidMount() {
        document.title = "Master Data";
    }

    render() {
        const { data } = this.state;
        const rows = data.map((row, index) => {
            return (
                <tr key={index}>
                    <td>{row.name}</td>
                    <td>{row.capacity}</td>
                    <td>{row.status}</td>
                    <td>
                        <a className="btn btn-primary btn-sm mr-1" href="#"><i className="fa fa-eye mr-1" />
                            Show
                        </a>
                        <a className="btn btn-warning btn-sm mr-1" href="#"><i className="fa fa-edit mr-1" />
                            Edit
                        </a>
                        <a className="btn btn-danger btn-sm" data-confirm="Are you sure?" data-method="delete" href="#" rel="nofollow"><i className="fa fa-trash-o mr-1" />
                            Delete
                        </a>
                    </td>
                </tr>
            )
        });

        return (
            <div>
                <TopArea title="Master Data" description="Organize all master data" />
                <section className="section-gap">
                    <div className="container">
                        <div className="row justify-content-center pb-30">
                            <div className="col-lg-8">
                                <div className="section-title text-center">
                                    <h1>Meeting Rooms List</h1>
                                    <span></span>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="form-group col-md-3 offset-md-9 align-self-end">
                                <button type="button" className="btn btn-primary w-100" data-toggle="modal" data-target="#modal">
                                    <i className="fa fa-plus mr-1"></i>
                                    Add Meeting Room
                                </button>
                            </div>
                        </div>
                        <div className="table-responsive">
                            <table className="table tile table-bordered">
                                <thead>
                                    <tr>
                                        <th>
                                            Name
                                        </th>
                                        <th>
                                            Capacity
                                        </th>
                                        <th>
                                            Status
                                        </th>
                                        <th className="actions">
                                            Action
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {rows}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </section>
                <Modal />
            </div>
        );
    }
}

export default Master;


