import React, { Component } from 'react';
import TopArea from '../fragments/TopArea';

class Settings extends Component {
    state = {
        "user": {
            "name": "User",
            "email": "user@mail.com",
        }
    };
    render() {
        const Container = props => {
            return (
                <div className="row mb-5">
                    <div className="col-md-6 offset-md-3 mb-3">
                        <h2>{props.title}</h2>
                    </div>
                    <div className="col-md-6 offset-md-3">
                        {props.children}
                    </div>
                </div>
            );
        };

        return (
            <div>
				<TopArea title="Settings" description="Office App" />
				<section className="section-gap">
					<div className="container">
						<div className="row d-flex justify-content-center pb-30">
							<div className="col-lg-8">
								<div className="section-title text-center">
									<h1>Edit Profile</h1>
								</div>
							</div>
						</div>
                        <Container title="Basic Information">
                            <form method="post">
                                <div className="form-group">
                                    <label htmlFor="name">Name</label>
                                    <input type="text" className="form-control" id="name" name="name" value={`${this.state.user.name}`} />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="email">Email</label>
                                    <input type="email" className="form-control" id="email" name="email" value={`${this.state.user.email}`} />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="password">Password</label>
                                    <input type="password" className="form-control" id="password" name="password" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="password-confirmation">Confirm Password</label>
                                    <input type="password" className="form-control" id="password-confirmation" name="password-confirmation" />
                                </div>
                                <button className="btn btn-primary btn-success">Update profile</button>
                            </form>
                        </Container>
                        <Container title="Reset Token">
                            <a class="btn btn-warning" href="#">Reset my token</a>
                        </Container>
                        <Container title="Cancel Account">
                            <a class="btn btn-danger" href="#">Cancel my account</a>
                        </Container>
                        <Container title="Language">
                            <a class="btn btn-primary mr-1" href="#">Indonesia</a>
                            <a class="btn btn-primary mr-1" href="#">English</a>
                        </Container>
					</div>
				</section>
            </div>
        );
    }
}

export default Settings


