import React, { Component } from 'react';

import TopArea from '../fragments/TopArea';

class Login extends Component {
    componentDidMount() {
        document.title = "Login";
    }

    render() {
        return (
            <div>
                <TopArea title="Login" description="Office App" />
                <section className="login-form-area section-gap">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-6 offset-md-3">
                                <div className="card-container">
                                    <form className="form-signin">
                                        <span id="reauth-email" className="reauth-email" />
                                        <label className="text-uppercase" htmlFor="username">Username or email address</label>
                                        <input type="username" id="username" className="form-control" placeholder="Username or Email" required />
                                        <label className="text-uppercase pt-10" htmlFor="password">Passoword</label>
                                        <input type="password" id="inputPassword" className="form-control" placeholder="Password" required />
                                        <div id="remember" className="checkbox mt-10">
                                            <label>
                                                <input type="checkbox" defaultValue="remember-me" /> Remember me
                                            </label>
                                        </div>
                                        <button className="btn btn-lg btn-primary btn-block btn-signin text-uppercase" type="submit">Log in</button>
                                    </form>{/* /form */}
                                    <a href="#" className="mt-10 forgot-password">
                                        Forgot the password?
                                    </a>
                                </div>{/* /card-container */}
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}

export default Login;


